import React from "react";

function NavBar() {
	return (
		<div>
			<header>
				<nav className="navbar fixed-top navbar-expand-lg navbar-dark indigo">
					<a className="navbar-brand" href="#">
						<strong>Navbar</strong>
					</a>
					<button
						className="navbar-toggler"
						type="button"
						data-toggle="collapse"
						data-target="#navbarSupportedContent"
						aria-controls="navbarSupportedContent"
						aria-expanded="false"
						aria-label="Toggle navigation"
					>
						<span className="navbar-toggler-icon"></span>
					</button>
					<div className="collapse navbar-collapse" id="navbarSupportedContent">
						<ul className="navbar-nav mr-auto">
							<li className="nav-item active">
								<a className="nav-link" href="#">
									Home <span className="sr-only">(current)</span>
								</a>
							</li>
							<li className="nav-item">
								<a className="nav-link" href="#">
									Link
								</a>
							</li>
							<li className="nav-item">
								<a className="nav-link" href="#">
									Profile
								</a>
							</li>
						</ul>
					</div>
				</nav>

				<div className="view intro-2">
					<div className="full-bg-img">
						<div className="mask rgba-black-light flex-center">
							<div className="container text-center white-text">
								<div className="white-text text-center wow fadeInUp">
									<h2>This Navbar is fixed</h2>
									<h5>
										It will always stay visible on the top, even when you scroll
										down
									</h5>
									<br />
									<p>
										Full page intro with background image will be always
										displayed in full screen mode, regardless of device{" "}
									</p>
								</div>
							</div>
						</div>
					</div>
				</div>
			</header>
		</div>
	);
}

export default NavBar;
